  </div>
</template>
<script>
  import Message from  '@/components/message/message'
  import CaseMore from  '@/components/case-more/case-more'
  export default {
    data(){
      return{
        add_class_status:false
      }
    },
    watch:{
      add_class_status(newval,oldval) {
        console.log(newval)
      }
    },
    mounted(){

        window.addEventListener('scroll',()=>{

          if(window.location.href.indexOf('exclusive') > -1){
          var two_price_H = document.getElementById('two_price').offsetHeight;
          var scrolltop = document.documentElement.scrollTop || document.body.scrollTop;
          if(scrolltop > two_price_H){
            this.add_class_status = true
          }else{
            this.add_class_status = false
          }
          }
        })
      document.documentElement.scrollTop = document.body.scrollTop =0;


    },
    methods:{

    },
    components:{
      Message,
      CaseMore
    }

  }



</script>
