    background-repeat no-repeat
    background-position center center
    background-size 1920px
    height 666px
    padding-top 130px
    text-align center
    img
      width 100%
    .left-img
      float left
      width 50%
      opacity: 0
      transform: translateX(-350px);
      img
        width 580px
    .right-img
      float right
      width 50%
      opacity: 0;
      transform: translateX(350px);
      img
        width 580px
    .none-animate
      opacity: 1;
      transition: all .8s;
      transform: translateX(0);

</style>





